import Home from "@/views/Home";
import About from "@/views/About";
import Blog from "@/views/Blog";
import Message from "@/views/Message";
import Project from "@/views/Project";
// import BlogDetail from "@/views/Blog/Detail";

export default [
    { name: "Home", path: "/", component: Home, meta: { title: '' } },
    { name: "About", path: "/about", component: About, meta: { title: '', transition: 'slide-left' } },
    { name: "BaseInfo", path: "/about/baseInfo", component: () => import('@/views/About/components/BaseInfo'), meta: { title: '', transition: 'slide-right' } },
    // { name: "Blog", path: "/blog", component: Blog, meta: { title: '' } },
    { name: "MyBlog", path: "/myBlog", component: () => import('@/views/MyBlog'), meta: { title: '' } },
    // { name: "CategoryBlog", path: "/blog/cate/:categoryId", component: Blog },
    { name: "CategoryBlog", path: "/myBlog/cate/:categoryId", component: () => import('@/views/MyBlog'), meta: { title: '' } },
    // { name: "BlogDetail", path: "/blog/:id", component: BlogDetail, meta: { title: '' } },
    { name: "BlogDetail", path: "/myBlog/", component: () => import('@/views/MyBlog/components/BlogDetail'), meta: { title: '' } },
    { name: "Message", path: "/message", component: Message, meta: { title: '' } },
    { name: "Project", path: "/project", component: Project, meta: { title: '&' } }
]